import React, { Component, Fragment } from 'react';

import PropTypes from 'prop-types';
import { inject, observer} from 'mobx-react';
import AppStore from '../../store/AppStore3';
import loadable from '@loadable/component';
import { LazyRenderer, EmptyComponent } from './../../utility/lazyRenderer';

import Page3Card1 from './subComponent/page3/page3card1';
import Page3Card2 from './subComponent/page3/page3card2';
import Page3Card3 from './subComponent/page3/page3card3';
import Page3Card4 from './subComponent/page3/page3card4';
import Page3Card5 from './subComponent/page3/page3card5';
import Page3Card6 from './subComponent/page3/page3card6';
import Page3Card7 from './subComponent/page3/page3card7';
import Page3Card8 from './subComponent/page3/page3card8';



@inject('appStore')
@observer
class LandingPage3 extends Component {
  constructor(props) {
    super(props);
}

static propTypes = {
    appStore: PropTypes.object.isRequired,
};

static defaultProps = {
    appStore: new AppStore(),
};

  componentDidMount(){
    if(typeof(landingPageDidMount)!=="undefined"){
      landingPageDidMount=true;
    }
  }
  
  render() {
    const { data } = this.props.appStore;
    return (
      <div>
        {!data.cards[0].loadError && <Page3Card1 data={data.cards[0]} lang={data.lang} appStore={this.props.appStore}></Page3Card1>}
{!data.cards[1].loadError && <Page3Card2 data={data.cards[1]} lang={data.lang} appStore={this.props.appStore}></Page3Card2>}
{!data.cards[2].loadError && <Page3Card3 data={data.cards[2]} lang={data.lang} appStore={this.props.appStore}></Page3Card3>}
{!data.cards[3].loadError && <Page3Card4 data={data.cards[3]} lang={data.lang} appStore={this.props.appStore}></Page3Card4>}
{!data.cards[4].loadError && <Page3Card5 data={data.cards[4]} lang={data.lang} appStore={this.props.appStore}></Page3Card5>}
{!data.cards[5].loadError && <Page3Card6 data={data.cards[5]} lang={data.lang} appStore={this.props.appStore}></Page3Card6>}
{!data.cards[6].loadError && <Page3Card7 data={data.cards[6]} lang={data.lang} appStore={this.props.appStore}></Page3Card7>}
{!data.cards[7].loadError && <Page3Card8 data={data.cards[7]} lang={data.lang} appStore={this.props.appStore}></Page3Card8>}

      </div>
    )
  }
}

export default LandingPage3;
