import axios from 'axios';
import PropTypes from 'prop-types';
import {makeObservable} from 'mobx';
import {observable,computed,action,autorun} from 'mobx';

import { parallelDataStoreFetch } from '../utility/fetchUtils';
import FeaturedStoriesAdapter from '../dataAdapters/FeaturedStoriesAdapter';


export default class AppStore {

  data = null;

  title = null;

  constructor(initialState) {
    makeObservable(this, {
      data: observable,
      title: observable,
      initialiseState: action,
      onloadcards5_c0: action,
    });
    this.initialiseState(initialState);
  }

  initialiseState(initialState) {
    this.data = initialState ? initialState.appStore.data : null;
    this.title = initialState ? initialState.appStore.title : null;
  }
 
  toJson() {
    return {
      data: this.data,
      title: this.title,
    };
  }


  onloadcards5_c0 = async function (){ 
const datastoreEndPoints = [
		{
		endpoint :"featured-stories-mobile",
		useCache :false,
		endpointurl :`http://api-news.dailyhunt.in/v2/posts/feed/HASHTAG/eb9d02d10ecd6077e2c3db67746e411d?entityTitle=HASHTAG&edition=india&section=news&isHome=true&feedConfigKey=TOPIC_20_HTML_VIDEO_CHRONO&langCode=${this.data.lang}`,
		},
	]
;
const featuredStoriesAdapter = new FeaturedStoriesAdapter();
    const dataCache = await parallelDataStoreFetch(datastoreEndPoints); 
this.data.cards[5].children[0].children = featuredStoriesAdapter.adaptData(this.data.cards[5].children[0].children,dataCache['featured-stories-mobile'], );
}


  
  

}
